@use './vars.scss';
@use './mixins.scss';

.error-404,
.error-500 {
  font-family: vars.$font-texte;
  min-height: 100vh;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  
  h1 {
    font-size: vars.$title-fontsize;
    margin-bottom: 1rem;

    @include mixins.tablet {
      font-size: 3rem;
    }

    @include mixins.desktop {
      font-size: 4rem;
    }
  }

  p {
    font-size: vars.$text-fontsize;
    margin-bottom: 1.5rem;
    max-width: 600px;
  }

  a {
    font-size: vars.$subtitle-fontsize;
    text-decoration: none;
    border-bottom: 2px solid transparent;
    transition: border-color 0.3s ease, color 0.3s ease;

    &:hover,
    &:focus {
      outline: none;
    }
  }
}

.error-404 {
  color: vars.$color-black;
  background-color: vars.$color-lightblue;

  h1 {
    color: vars.$color-purple;
  }

  a {
    color: vars.$color-blue;
    
    &:hover,
    &:focus {
      color: vars.$color-darkpurple;
      border-color: vars.$color-darkpurple;
    }
  }
}

.error-500 {
  color: vars.$color-white;
  background-color: vars.$color-darkpurple;

  h1 {
    color: vars.$color-lightblue;
  }

  a {
    color: vars.$color-blue;

    &:hover,
    &:focus {
      color: vars.$color-purple;
      border-color: vars.$color-purple;
    }
  }
}
