body {
  background-color: #fff;
  margin: 0;
  font-family: Verdana\, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.4;
}

.contenu {
  background: linear-gradient(to bottom right, #0000, #bb298b, #3b61e6, #3dbede);
  flex-flow: row-reverse;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 8%;
  display: flex;
}

@media (width <= 768px) {
  .contenu {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
  }
}

.img_main {
  border-radius: 20px;
  width: 50%;
  height: auto;
}

@media (width <= 768px) {
  .img_main {
    border-radius: 10px;
    width: 100%;
    margin: 25%;
  }
}

.main__text p, .main__text h1 {
  color: #fff;
  margin-bottom: 2rem;
}

.main__text p {
  font-size: 3.5rem;
}

@media (width <= 768px) {
  .main__text p {
    font-size: 2rem;
  }
}

@media screen and (width <= 992px) {
  .main__text p {
    font-size: 2.75rem;
  }
}

.main__text h1 {
  margin-top: 1rem;
  font-size: 3.5rem;
  font-weight: bold;
}

@media (width <= 768px) {
  .main__text h1 {
    font-size: 2.5rem;
  }
}

@media screen and (width <= 992px) {
  .main__text h1 {
    font-size: 3rem;
  }
}

.carousel {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.titre {
  text-align: center;
  margin-bottom: 2rem;
  font-family: Verdana\, Arial, sans-serif;
  font-size: 32px;
}

@media (width <= 768px) {
  .titre {
    margin-bottom: 1rem;
    font-size: 2rem;
  }
}

.cardes {
  justify-content: center;
  gap: 1rem;
  width: 100%;
  padding: 1rem 0;
  display: flex;
  overflow-x: auto;
}

@media (width <= 768px) {
  .cardes {
    gap: .5rem;
    padding: .5rem 0;
  }
}

.carde {
  color: #000;
  justify-content: center;
  align-items: center;
  font-family: Verdana\, Arial, sans-serif;
  font-size: 18px;
  display: flex;
}

@media (width <= 768px) {
  .carde {
    font-size: .9rem;
  }
}

.présentation {
  background-color: #3b61e6;
  flex-flow: row-reverse wrap;
  justify-content: space-around;
  align-items: center;
  gap: 2rem;
  margin: 3%;
