}

@media (width <= 768px) {
  .présentation {
    flex-direction: column;
    margin: 1rem 0;
  }
}

.présentation__img {
  border-radius: 10px;
  width: 1000px;
  max-width: 100%;
}

@media (width <= 768px) {
  .présentation__img {
    border-radius: 5px;
    width: 100%;
  }
}

.présentation__contenu {
  flex-direction: column;
  gap: 1.5rem;
