  }
}

.présentation__btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(to bottom right, #0000, #bb298b, #3b61e6, #3dbede);
  border: none;
  border-radius: 25px;
  width: fit-content;
  padding: .5rem 1.5rem;
  font-size: 1rem;
}

@media (width <= 768px) {
  .présentation__btn {
    padding: .4rem 1rem;
    font-size: .85rem;
  }
}

.about_witness_contain0 {
  display: flex;
}

@media (width <= 768px) {
  .about_witness_contain0 {
    flex-direction: column;
  }

  .pied_de_page {
    flex-direction: column;
    padding: 1rem;
    font-size: .9rem;
  }
}

.pied_de_page_a {
  color: #fff;
  font-family: Verdana\, Arial, sans-serif;
  text-decoration: none;
}

@media (width <= 768px) {
  .pied_de_page_a {
    font-size: .9rem;
  }

  .pied_de_page_img2 {
    height: 30px;
  }

  .contact {
    gap: .3rem;
  }

  .copy {
    font-size: .75rem;
  }
}

.auth-page {
  background: linear-gradient(#3b61e6, #fff);
  flex-direction: column;
  align-items: stretch;
  display: flex;
}

.auth-main {
  flex: 1;
  max-width: 400px;
  margin: 2rem auto;
  padding: 8%;
}

@media screen and (width <= 992px) {
  .auth-main {
    max-width: 500px;
    padding: 0 2rem;
  }
}

@media screen and (width >= 993px) {
  .auth-main {
    max-width: 600px;
  }
}

.auth-main .auth-title {
  color: #bb298b;
  text-align: center;
  margin-bottom: .5rem;
  padding: 20%;
  font-size: 32px;
}

@media (width <= 768px) {
  .auth-main .auth-title {
    font-size: 36px;
  }
}

@media screen and (width <= 992px) {
  .auth-main .auth-title {
    font-size: 40px;
  }
}

@media screen and (width >= 993px) {
  .auth-main .auth-title {
    font-size: 48px;
  }
}

.auth-main .auth-subtitle {
  color: #833daf;
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 24px;
}

@media screen and (width <= 992px) {
  .auth-main .auth-subtitle {
    font-size: 28px;
  }
}

@media screen and (width >= 993px) {
  .auth-main .auth-subtitle {
    font-size: 32px;
  }
}

.auth-main form {
  background: #fff;
  border-radius: 10px;
  max-width: 400px;
  margin: 0 auto;
  padding: 2rem;
  box-shadow: 0 2px 8px #0000001a;
}

@media screen and (width <= 992px) {
  .auth-main form {
    max-width: 500px;
    padding: 3rem;
  }
}

@media screen and (width >= 993px) {
  .auth-main form {
    max-width: 600px;
  }
}

.auth-main form label {
  margin-top: 1rem;
  font-weight: bold;
  display: block;
}

.auth-main form input, .auth-main form select, .auth-main form button {
  box-sizing: border-box;
  border: 1px solid #3dbede;
  border-radius: 5px;
  width: 100%;
  margin-top: .5rem;
  padding: .75rem;
  font-size: 18px;
}

@media screen and (width <= 992px) {
  .auth-main form input, .auth-main form select, .auth-main form button {
    padding: 1rem;
  }
}

.auth-main form .button {
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-top: 1.5rem;
  padding: .75rem;
  font-size: 1rem;
  font-weight: bold;
  transition: background-color .3s, transform .2s;
  box-shadow: 0 2px 4px #0000001a;
}

.auth-main form .button:hover, .auth-main form .button:focus {
  outline: none;
  transform: scale(1.02);
}

.auth-main form .button:focus {
  box-shadow: 0 0 0 3px #6464fa66;
}

.auth-main form .button:disabled {
  color: #666;
  cursor: not-allowed;
  background-color: #ccc;
}

.auth-main form .forgot-password-link {
  text-align: right;
  margin-top: .5rem;
}

.auth-main form .checkbox-wrapper {
  align-items: center;
  gap: 0;
  margin-top: 1rem;
  display: flex;
}

.auth-main form .checkbox-wrapper input[type="checkbox"] {
  cursor: pointer;
  margin: 0;
  transform: scale(1.2);
}

.auth-main form .checkbox-wrapper label {
  font-size: 18px;
  font-weight: normal;
  line-height: 1.4;
}

.auth-main form .checkbox-wrapper label a {
  color: #bb298b;
  text-decoration: underline;
}

.auth-main form .checkbox-wrapper label a:hover {
  color: #bb298be6;
}

.auth-main form .confirm-password {
  margin-top: 1rem;
}

.auth-main form .confirm-password input {
  background-color: #f9f9f9;
  border: 1px solid #ccc;
}

.auth-main form .confirm-password label {
  color: #666;
}

.auth-main form .required-note {
  color: #833daf;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 18px;
}

.auth-main form .required-note span {
  color: red;
  font-weight: bold;
}

.auth-main .sr-only {
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.auth-main .skip-link {
  color: #000;
  z-index: 1000;
  background-color: #fff;
  width: 1px;
  height: 1px;
  padding: .5rem;
  font-weight: bold;
  text-decoration: none;
  position: absolute;
  top: auto;
  left: -9999px;
  overflow: hidden;
}

.auth-main .skip-link:focus {
  outline: none;
  width: auto;
  height: auto;
  margin: 1rem;
  display: inline-block;
  position: static;
  top: auto;
  left: auto;
  box-shadow: 0 0 0 3px #005fcc;
}

.about_witness_contain0 {
  background-color: #fff;
  flex-direction: row;
